import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ProfilePage } from '../profile/profile';
import { ThePostPage } from '../the-post/the-post';

@IonicPage()
@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications.html',
})
export class NotificationsPage {
  // loggedUser: string = "y_lumberjack";
  // fromUser: string = "DonaldDuck64";
  // postID: string = "12";
  // QUERY /api/notifications/ WHERE to = logged_user
  // "MY POSTS" (prolly just gonna get {ID, TO, FROM} from notifications collection)
  notifications: any[];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.notifications =  [
      {
        username: "y_lumberjack",
        who: "DonaldDuck64",
        type: "liked",
        post: {
          id: "14",
          username: "y_lumberjack",
          category: "Classic",
          challenge: "Slam Dunk!",
          description: "Here is another sample description.",
          location: "Jaboticabal-SP",
          views: 105,
          likes: 50,
          dislikes: 10,
          date: "10/10/2017"
        }
      },
      {
        username: "y_lumberjack",
        who: "Japa",
        type: "commented",
        comment: "asdfioqjweoq...",
        post: {
          id: "11",
          username: "y_lumberjack",
          category: "Classic",
          challenge: "Do what you can't",
          description: "Just a sample description here.",
          location: "Jaboticabal-SP",
          views: 105,
          likes: 50,
          dislikes: 10,
          date: "10/10/2017"
        }
      },
      {
        username: "y_lumberjack",
        who: "Rolao",
        type: "followed"
      }
    ]
  }

  ionViewDidLoad() {
    //console.log(this.notifications);
  }


  goToProfile(username){
    this.navCtrl.push(ProfilePage, {"username": username});    
  }

  goToPost(postID){
    this.navCtrl.push(ThePostPage, postID);        
  }

  // PULLDOWN REFRESH
  doRefresh(refresher) {
    console.log('Begin async operation', refresher);

    setTimeout(() => {
      console.log('Async operation has ended');
      refresher.complete();
    }, 1000);
  }
}



